export default {
  props: {
    list: {
      default: null
    }
  },
  methods: {
    formatDate (date) {
      this.$dateFormat(date)
    },
    getLink (type, item) {
      if (type === 'detail') {
        this.$router.push({name: 'BlockDetail', params: {cmd: 'qryblo', args: item.num}})
      } else {
        this.$router.push({name: 'BlockRecord', query: {args: item.num}})
      }
    }
  }
}



