
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import indexNav from './indexNav'
import indexSearch from './indexSearch'
import indexBlock from './indexBlock'
import indexToken from './indexToken'
import indexVote from './indexVote'
import indexMortgage from './indexMortgage'
import indexExchange from './indexExchange'
import { getBlock, getTklist, getVlist, getMortglist, getExchangelist } from 'api'
export default {
data () {
return {
list: [],
page: 1,
noData: true,
loading: false,
activeNavKey: this.$route.query.navkey || 'block',
keyword: '',
navs: {
block: {
name: '区块',
placeholder: '请输入区块高度、区块哈希、交易哈希、钱包地址',
api: ''
},
token: {
name: 'Token',
placeholder: '请输入Token名称',
api: ''
},
vote: {
name: '投票合约',
placeholder: '请输入投票ID',
api: ''
},
mortgage: {
name: '抵押合约',
placeholder: '请输入抵押ID',
api: ''
},
exchange: {
name: '兑换合约',
placeholder: '请输入兑换合约地址',
api: ''
}
}
}
},
computed: {
activeNav () {
return this.navs[this.activeNavKey]
}
},
created () {
this.getList()
},
beforeRouteUpdate (to, from, next) {
this.activeNavKey = to.query.navkey || 'block'
this.getList()
next()
},
methods: {
updatePage (key) {
if (key === this.activeNavKey) {
this.activeNavKey = key
this.keyword = ''
this.search()
return false
}
this.list = []
this.page = 1
this.noData = true
this.loading = false
this.$router.replace({name: 'Index', query: {navkey: key}})
},
search () {
this.list = []
this.page = 1
this.noData = true
this.loading = false
if (this.activeNavKey === 'block' && this.keyword) {
this.blockSearch()
} else {
this.getList()
}
},

blockSearch () {




let keyword = this.keyword
if (keyword.startsWith('0x')) {
this.$router.push({name: 'BlockRecord', query: {acc: keyword}})
} else if (Number(keyword)) {
this.$router.push({name: 'BlockDetail', params: {cmd: 'qryblo', args: keyword}})
} else if (keyword && keyword.length > 45) {
this.$router.push({name: 'BlockTrade', params: {args: keyword}})
} else {
this.$router.push({name: 'BlockDetail', params: {cmd: 'qryhash', args: keyword}})
}
},
getList () {
if (this.loading) {
return
}
this.loading = true
let type = this.activeNavKey
let page = this.page
let keyword = this.keyword
if (type === 'block') {
getBlock({page, method: 'list'})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
if (type === 'token') {
getTklist({page, arg: keyword})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
if (type === 'vote') {
let arge = {page}
if (keyword !== '') {
arge.account = keyword
}
getVlist(arge)
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
if (type === 'mortgage') {
getMortglist({page, mortgId: keyword})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
if (type === 'exchange') {
getExchangelist({page, exchangeAddr: keyword})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
}
},
setList (res) {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
},
loadMore () {
this.noData = true
this.page += 1
this.getList()
}
},
components: {
XHeader,
indexNav,
indexSearch,
indexBlock,
indexToken,
indexVote,
indexMortgage,
indexExchange,
LoadMore
}
}



