export default {
  props: {
    placeholder: {
      type: String
    },
    keyword: {
      type: String,
      default: ''
    }
  },
  computed: {
    searchVal: {
      get () {
        return this.keyword
      },
      set (value) {
        this.$emit('update:keyword', value)
      }
    }
  },
  methods: {
    submit () {
      if (!this.keyword) {
        this.$vux.toast.text(this.placeholder)
        return false
      }
      this.$emit('search')
    }
  },
  components: {}
}



